@mixin screen_1184 {
  @media (max-width: 1184px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin screen_890 {
  @media (max-width: 890px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 800px) {
    @content;
  }
}

@mixin screen_680 {
  @media (max-width: 680px) {
    @content;
  }
}

@mixin mobile-medium {
  @media (max-width: 540px) {
    @content;
  }
}

@mixin mobile-small {
  @media (max-width: 400px) {
    @content;
  }
}